import * as React from "react"

import Layout from "../components/layout"
import { Link } from "gatsby";

const IndexPage = () => (
  <Layout>
    <h1 className="page-header">Фонд развития информационных и интеллектуальных систем</h1>
    <p className="text-lg">
      Мы занимаемся разработкой и поддержкой программного обеспечения для бюджетных организаций, а также организацией профориентационных мероприятий. <br/>
      Больше о наших проектах Вы можете узнать на <Link className="link-brand" to="/projects">странице с проектами</Link>.
    </p>
  </Layout>
)

export default IndexPage
